.header {
  z-index: 1000;
  padding: var(--space-md) 0;
  transition: all var(--transition-normal);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header--scrolled {
  background-color: var(--color-bg);
  border-bottom: 1px solid var(--color-border);
  box-shadow: var(--shadow-sm);
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  font-family: var(--font-display);
  color: var(--color-text);
  align-items: center;
  gap: 2px;
  font-size: 1.75rem;
  text-decoration: none;
  display: flex;
}

.header__logo:hover {
  text-decoration: none;
}

.header__logo-bracket {
  color: var(--color-text-muted);
}

.header__logo-text {
  color: var(--color-accent);
}

.header__nav {
  gap: var(--space-lg);
  display: flex;
}

.header__nav a {
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  transition: color var(--transition-fast);
  font-size: .875rem;
  text-decoration: none;
  position: relative;
}

.header__nav a:hover {
  color: var(--color-text);
  text-decoration: none;
}

.header__nav a:after {
  content: "";
  background-color: var(--color-accent);
  width: 0;
  height: 2px;
  transition: width var(--transition-fast);
  position: absolute;
  bottom: -4px;
  left: 0;
}

.header__nav a:hover:after {
  width: 100%;
}

.header__actions {
  align-items: center;
  gap: var(--space-md);
  display: flex;
}

.header__theme-toggle {
  align-items: center;
  gap: var(--space-xs);
  padding: var(--space-sm) var(--space-md);
  font-family: var(--font-mono);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  color: var(--color-text-muted);
  cursor: pointer;
  transition: all var(--transition-fast);
  background: none;
  font-size: .75rem;
  display: flex;
}

.header__theme-toggle:hover {
  border-color: var(--color-accent);
  color: var(--color-accent);
}

.header__theme-icon {
  font-size: .875rem;
}

.header__github {
  padding: var(--space-sm) var(--space-md);
  font-size: .8rem;
}

.header__mobile-toggle {
  padding: var(--space-sm);
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  gap: 5px;
  display: none;
}

.header__mobile-toggle span {
  background-color: var(--color-text);
  width: 24px;
  height: 2px;
  transition: all var(--transition-fast);
  display: block;
}

@media (width <= 768px) {
  .header__nav {
    background-color: var(--color-bg);
    border-bottom: 1px solid var(--color-border);
    padding: var(--space-lg);
    gap: var(--space-md);
    flex-direction: column;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
  }

  .header__nav--open, .header__mobile-toggle {
    display: flex;
  }

  .header__github, .header__theme-label {
    display: none;
  }
}

.hero {
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  padding-top: 80px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero__bg-pattern {
  background-image: radial-gradient(circle at 20% 80%, var(--color-accent) 0%, transparent 25%), radial-gradient(circle at 80% 20%, var(--color-accent-secondary) 0%, transparent 25%);
  opacity: .03;
  pointer-events: none;
  position: absolute;
  inset: 0;
}

.hero__container {
  gap: var(--space-xl);
  grid-template-columns: 1fr 1fr;
  align-items: center;
  display: grid;
}

.hero__content {
  max-width: 600px;
}

.hero__logo {
  font-family: var(--font-display);
  margin-bottom: var(--space-lg);
  align-items: center;
  font-size: clamp(4rem, 10vw, 7rem);
  display: flex;
  position: relative;
}

.hero__bracket {
  color: var(--color-text-muted);
}

.hero__v {
  color: var(--color-accent);
}

.hero__rest {
  color: var(--color-text);
  opacity: 0;
  transition: all .4s;
  transform: translateX(-20px);
}

.hero__rest--visible {
  opacity: 1;
  transform: translateX(0);
}

.hero__cursor {
  background-color: var(--color-accent);
  width: 4px;
  height: .8em;
  margin-left: 2px;
  animation: .8s infinite blink;
  display: inline-block;
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }

  51%, 100% {
    opacity: 0;
  }
}

.hero__tagline {
  font-family: var(--font-display);
  color: var(--color-text);
  margin-bottom: var(--space-md);
  opacity: 0;
  font-size: clamp(1.5rem, 4vw, 2.5rem);
}

.hero__description {
  color: var(--color-text-muted);
  margin-bottom: var(--space-xl);
  opacity: 0;
  font-size: 1.125rem;
  line-height: 1.8;
}

.hero__highlight {
  color: var(--color-accent);
  font-weight: 500;
}

.hero__cta {
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
  opacity: 0;
  flex-wrap: wrap;
  display: flex;
}

.hero__cta .btn-primary span {
  color: #fff9;
}

.hero__stats {
  gap: var(--space-xl);
  opacity: 0;
  flex-wrap: wrap;
  display: flex;
}

.hero__stat {
  flex-direction: column;
  display: flex;
}

.hero__stat-number {
  font-family: var(--font-display);
  color: var(--color-accent);
  font-size: 2rem;
}

.hero__stat-label {
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  text-transform: uppercase;
  letter-spacing: .1em;
  font-size: .75rem;
}

.hero__demo {
  opacity: 0;
}

.hero__terminal {
  background-color: var(--color-terminal-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-retro);
  overflow: hidden;
}

.hero__terminal-header {
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-md);
  background-color: #ffffff0d;
  border-bottom: 1px solid #ffffff1a;
  display: flex;
}

.hero__terminal-dot {
  background-color: #fff3;
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.hero__terminal-dot:first-child {
  background-color: #ff5f56;
}

.hero__terminal-dot:nth-child(2) {
  background-color: #ffbd2e;
}

.hero__terminal-dot:nth-child(3) {
  background-color: #27ca40;
}

.hero__terminal-title {
  font-family: var(--font-mono);
  color: #ffffff80;
  margin-left: auto;
  font-size: .75rem;
}

.hero__terminal-code {
  padding: var(--space-lg);
  font-family: var(--font-mono);
  color: var(--color-terminal-text);
  margin: 0;
  font-size: .9rem;
  line-height: 1.8;
  overflow-x: auto;
}

.hero__terminal-code code {
  color: inherit;
}

.hero__scroll-indicator {
  bottom: var(--space-xl);
  align-items: center;
  gap: var(--space-sm);
  color: var(--color-text-muted);
  font-family: var(--font-mono);
  flex-direction: column;
  font-size: .75rem;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.hero__scroll-line {
  background: linear-gradient(to bottom, var(--color-text-muted), transparent);
  width: 1px;
  height: 40px;
  animation: 2s ease-in-out infinite scrollPulse;
}

@keyframes scrollPulse {
  0%, 100% {
    opacity: .3;
    transform: scaleY(1);
  }

  50% {
    opacity: 1;
    transform: scaleY(1.2);
  }
}

@media (width <= 968px) {
  .hero__container {
    text-align: center;
    grid-template-columns: 1fr;
  }

  .hero__content {
    max-width: 100%;
  }

  .hero__logo {
    justify-content: center;
  }

  .hero__description {
    margin-left: auto;
    margin-right: auto;
  }

  .hero__cta, .hero__stats {
    justify-content: center;
  }

  .hero__demo {
    max-width: 500px;
    margin: 0 auto;
  }
}

@media (width <= 480px) {
  .hero__cta {
    flex-direction: column;
  }

  .hero__cta .btn {
    justify-content: center;
    width: 100%;
  }

  .hero__stats {
    gap: var(--space-lg);
  }
}

.features {
  background-color: var(--color-bg);
}

.features__header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto var(--space-xl);
}

.features__label {
  font-family: var(--font-mono);
  color: var(--color-accent);
  margin-bottom: var(--space-md);
  font-size: .875rem;
  display: inline-block;
}

.features__header h2 {
  margin-bottom: var(--space-md);
}

.features__subtitle {
  color: var(--color-text-muted);
  font-size: 1.125rem;
}

.features__grid {
  gap: var(--space-lg);
  margin-bottom: var(--space-2xl);
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  display: grid;
}

.feature-card {
  background-color: var(--color-bg-alt);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: var(--space-xl);
  transition: all var(--transition-normal);
  position: relative;
}

.feature-card:hover {
  box-shadow: var(--shadow-md);
  border-color: var(--color-accent);
  transform: translateY(-4px);
}

.feature-card__icon {
  font-family: var(--font-display);
  color: var(--color-accent);
  margin-bottom: var(--space-md);
  opacity: .9;
  font-size: 2.5rem;
}

.feature-card__tag {
  top: var(--space-lg);
  right: var(--space-lg);
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  text-transform: uppercase;
  letter-spacing: .1em;
  padding: var(--space-xs) var(--space-sm);
  background-color: var(--color-bg);
  border-radius: var(--border-radius);
  font-size: .7rem;
  position: absolute;
}

.feature-card__title {
  font-family: var(--font-display);
  margin-bottom: var(--space-sm);
  font-size: 1.75rem;
}

.feature-card__description {
  color: var(--color-text-muted);
  line-height: 1.7;
}

.features__highlight {
  gap: var(--space-xl);
  background-color: var(--color-bg-alt);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: var(--space-xl);
  box-shadow: var(--shadow-retro);
  grid-template-columns: 1fr 1fr;
  align-items: center;
  display: grid;
}

.features__highlight-label {
  font-family: var(--font-mono);
  color: var(--color-accent);
  text-transform: uppercase;
  letter-spacing: .1em;
  margin-bottom: var(--space-sm);
  font-size: .75rem;
  display: inline-block;
}

.features__highlight-content h3 {
  font-family: var(--font-display);
  margin-bottom: var(--space-md);
  font-size: 1.75rem;
}

.features__highlight-content p {
  color: var(--color-text-muted);
  line-height: 1.7;
}

.features__highlight-code {
  background-color: var(--color-terminal-bg);
  border-radius: var(--border-radius);
  padding: var(--space-lg);
  overflow-x: auto;
}

.features__highlight-code pre {
  margin: 0;
}

.features__highlight-code code {
  font-family: var(--font-mono);
  color: var(--color-terminal-text);
  font-size: .875rem;
  line-height: 1.8;
}

@media (width <= 968px) {
  .features__highlight {
    grid-template-columns: 1fr;
  }

  .features__highlight-content {
    text-align: center;
  }

  .features__highlight-content p {
    max-width: 100%;
  }
}

@media (width <= 480px) {
  .features__grid {
    grid-template-columns: 1fr;
  }

  .feature-card {
    padding: var(--space-lg);
  }
}

.code-demo {
