  max-width: 700px;
  margin: 0 auto var(--space-xl);
}

.code-demo__label {
  font-family: var(--font-mono);
  color: var(--color-accent);
  margin-bottom: var(--space-md);
  font-size: .875rem;
  display: inline-block;
}

.code-demo__header h2 {
  margin-bottom: var(--space-md);
}

.code-demo__subtitle {
  color: var(--color-text-muted);
  font-size: 1.125rem;
}

.code-demo__tabs {
