  background-color: var(--color-bg);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
}

.code-demo__info h3 {
  font-family: var(--font-display);
  margin-bottom: var(--space-sm);
  font-size: 1.5rem;
}

.code-demo__info p {
  color: var(--color-text-muted);
  font-size: .9rem;
  line-height: 1.6;
}

.code-demo__editor {
  background-color: var(--color-terminal-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-retro);
  overflow: hidden;
}

.code-demo__editor-header {
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-md);
  background-color: #ffffff08;
  border-bottom: 1px solid #ffffff14;
  display: flex;
}

.code-demo__dot {
  background-color: #ffffff26;
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.code-demo__dot:first-child {
  background-color: #ff5f56;
}

.code-demo__dot:nth-child(2) {
  background-color: #ffbd2e;
}

.code-demo__dot:nth-child(3) {
  background-color: #27ca40;
}

.code-demo__filename {
  margin-left: var(--space-md);
  font-family: var(--font-mono);
  color: #ffffff80;
  font-size: .75rem;
}

.code-demo__status {
  align-items: center;
  gap: var(--space-xs);
  font-family: var(--font-mono);
  color: #27ca40;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-left: auto;
  font-size: .7rem;
  display: flex;
}

.code-demo__status-dot {
  background-color: #27ca40;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  animation: 2s ease-in-out infinite pulse;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .4;
  }
}

.code-demo__code {
  padding: var(--space-lg);
  font-family: var(--font-mono);
  color: var(--color-terminal-text);
  min-height: 300px;
  margin: 0;
  font-size: .85rem;
  line-height: 1.8;
  overflow-x: auto;
}

.code-demo__code code {
  color: inherit;
  white-space: pre;
}

.code-demo__hint {
  justify-content: center;
  align-items: center;
  gap: var(--space-sm);
  margin-top: var(--space-lg);
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  font-size: .8rem;
  display: flex;
}

.code-demo__hint-icon {
  color: var(--color-accent);
  font-size: 1rem;
}

@media (width <= 868px) {
  .code-demo__content {
    grid-template-columns: 1fr;
  }

  .code-demo__info {
    text-align: center;
  }
}

@media (width <= 480px) {
  .code-demo__tabs {
    gap: var(--space-xs);
  }

  .code-demo__tab {
    padding: var(--space-xs) var(--space-sm);
    font-size: .75rem;
  }

  .code-demo__code {
    padding: var(--space-md);
    font-size: .75rem;
  }
}

.sound-library {
  background-color: var(--color-bg);
}

.sound-library__header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto var(--space-xl);
}

.sound-library__label {
  font-family: var(--font-mono);
  color: var(--color-accent);
  margin-bottom: var(--space-md);
  font-size: .875rem;
  display: inline-block;
}

.sound-library__header h2 {
  margin-bottom: var(--space-md);
}

.sound-library__subtitle {
  color: var(--color-text-muted);
  font-size: 1.125rem;
}

.sound-library__grid {
  gap: var(--space-lg);
  margin-bottom: var(--space-xl);
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  display: grid;
}

.sound-card {
  background-color: var(--color-bg-alt);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: var(--space-lg);
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.sound-card:before {
  content: "";
  background-color: var(--card-color);
  transform-origin: 0;
  height: 3px;
  transition: transform var(--transition-normal);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.sound-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.sound-card:hover:before {
  transform: scaleX(1);
}

.sound-card__header {
  margin-bottom: var(--space-md);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.sound-card__icon {
  font-family: var(--font-display);
  color: var(--card-color);
  font-size: 2rem;
}

.sound-card__count {
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  font-size: 1.5rem;
  font-weight: 700;
}

.sound-card__title {
  font-family: var(--font-display);
  margin-bottom: var(--space-md);
  font-size: 1.5rem;
}

.sound-card__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.sound-card__list li {
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  padding: var(--space-xs) 0;
  border-bottom: 1px solid var(--color-border);
  font-size: .75rem;
}

.sound-card__list li:last-child {
  border-bottom: none;
}

.sound-library__example {
  background-color: var(--color-terminal-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-retro);
  margin-bottom: var(--space-xl);
  overflow: hidden;
}

.sound-library__example-header {
  padding: var(--space-md) var(--space-lg);
  background-color: #ffffff08;
  border-bottom: 1px solid #ffffff14;
}

.sound-library__example-label {
  font-family: var(--font-mono);
  color: #ffffff80;
  font-size: .75rem;
}

.sound-library__example-content {
  padding: var(--space-lg);
}

.sound-library__example-content pre {
  margin: 0;
}

.sound-library__example-content code {
  font-family: var(--font-mono);
  color: var(--color-terminal-text);
  font-size: .85rem;
  line-height: 1.8;
}

.sound-library__cta {
  text-align: center;
}

.sound-library__cta p {
  color: var(--color-text-muted);
  margin-bottom: var(--space-md);
  max-width: 100%;
}

@media (width <= 768px) {
  .sound-library__grid {
    gap: var(--space-md);
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  }

  .sound-card {
    padding: var(--space-md);
  }
}

